<template>
  <div class="set_shop">
    <header class="set_header">
      <img src="../../assets/shopImage/join.png" alt="">
    </header>
    <div class="rule" v-for="items in rules">
      <div class="rule_left">
        <img :src="items.pic | imgFilter" alt="" >
      </div>
      <section class="rule_right">
          <div class="rule_list">
            <h2>{{items.name}}</h2>
          </div>
          <div>
            <a href="javascript:void(0)" class="shop_btn" @click="seeStore(items)">了解开店</a>
          </div>
      </section>
    </div>
  </div>
</template>
<script type="text/javascript">
  export default {
    data(){
      return {
          rules: [
            {
              pic:'fans.png',
              name: '',
              price: '',
            },
            {
              pic:'star_p.png',
              name: '',
              price: '',
            },
            {
              pic:'heart.png',
              name: '',
              price: '',
            }
          ]
      }
    },
    methods:{
      seeStore(item){
        let url = '/store/' + item.id;
        this.$router.push(url)
      }
    },
    mounted(){
      this.post('home/Shop/getDianpu', {}).then(res  => {
        if (res.data.code === 10000) {
          let info = res.data.info;
          this.rules.forEach((item, index) => {
            item['id'] = info[index].id;
            item['name'] = info[index].name;
            item['price'] = info[index].money;
          })
        }
      })
    },
    filters:{
      imgFilter(val){
        return 'static/shop/'+ val
      }
    }
  }
</script>
<style type="text/css" scoped>
  .set_shop {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
  }
  .set_header {
      width: 100%;
      padding-top: 3.76rem;
      background-color: #F2F2F2;
      position: relative;
  }
  .set_header>img {
    width: 100%;
    min-height: 100%;
    position: absolute;
    top: 0;
  }
  .rule {
    padding: 0 0.4rem;
    border-bottom: 1px solid #f5f5f5;
    display: flex;
    flex: 1;
    align-items: center;
  }
  .rule_left {
    width: 0.8rem;
    text-align: right;
  }
  .rule_left>img {
    height: 0.6rem;
    /*margin-top: 0.2rem;*/
  }
  .rule_img {
    height: 0.5rem;
  }
  .rule_img1 {
     height: 0.62rem;
  }
  .rule_img2 {
    height: 0.4rem;
  }
  .rule_right {
    margin-left:0.4rem;
    flex: 1;
    display: flex;
    justify-content: space-between;
  }
  .rule_list>h2 {
    font-size: 0.36rem;
    color: #111111;
    font-weight: bold;
  }
  .rule_list li {
    font-size: 0.24rem;
    color: #666666;
  }
  .shop_btn {
    /*margin-top: 0.2rem;*/
    display: inline-block;
    font-size: 0.24rem;
    color: #ff6f00;
    width: 1.4rem;
    height: 0.48rem;
    border:1px solid #ff6f00;
    border-radius: 10px;
    text-align: center;
    line-height: 0.48rem;
  }
</style>



